import React from 'react';
export class AutoTypingComponent extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      text: '',
      typingSpeed:100,
      isDeleting:false,
      loopNum:0,
      color:'yellow'
    };
  }

  componentDidMount() {
    this.handleType();
  }

  handleType = () => {
    const  dataText  = ['Programmer;',"Engineer;",'Web Developer;'];
    const colorSet =['gold','aqua','greenyellow']
    const { isDeleting, loopNum, text, typingSpeed } = this.state;
    const i = loopNum;
    const fullText = dataText[i];
    const currColor=colorSet[i];

    this.setState({
      text: isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1),
      typingSpeed: isDeleting ? 100 : 30
    });

    if (!isDeleting && text === fullText) {

      setTimeout(() => this.setState({ isDeleting: true }), 500);

    } else if (isDeleting && text === '') {

      this.setState({
        isDeleting: false,
        loopNum:  loopNum === dataText.length-1 ? 0 : loopNum+1,
        color:currColor
      });

    }

    setTimeout(this.handleType, typingSpeed);
  };

  render() {
    return (
      <div>
        <h2>I'm <span>{`{`}</span> <span style={{color:this.state.color}}>{this.state.text}</span> <span>{`}`}</span> </h2>
      </div>
    );
  }
}