import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { LightbulbFill } from 'react-bootstrap-icons';
import { FontAwesomeIcon, } from '@fortawesome/react-fontawesome';
import {
  html5, css3, bootstrap, angular, react, java,
  javascript, csharp, firebase, photoshop, database, nodejs
} from '../assets/index';
import { faHackerrank, faGithub, faLinkedin } from '@fortawesome/free-brands-svg-icons'
import { coder } from '../assets/index';

export class AboutComponent extends React.Component {

  render() {
    return (
      <div>
        <Container style={{  "paddingTop": "50px", "paddingBottom": "50px" }}>
          <Row>
            <Col md={4}>
              <img src={coder} alt="Profile" className='profileImage'></img>
            </Col>
            <Col md={8} style={{ backgroundColor: "rgba(255,255,255,0.1)", borderRadius: "8px" }}>
              <div  className="largetext" style={{ "textAlign": "left", textJustify: "center" }}>
                <h5 className="resumeHeader" >ABOUT</h5>
                <p  >
                  An enthusiastic Engineer and aims to work in a dynamic environment, to get
                  the best out of my skills which can be productive for the company as well as me and
                  I would like to enhance myself by discharging my duties in a versatile manner
                </p>
                <Row style={{ paddingBottom: "10px" }}>
                  <Col md="auto"> <LightbulbFill color="yellow" size={20} /><p><b> Qualification</b> : B.E - Electrical and Electronic Engineer</p></Col>
                  <Col md="auto"><LightbulbFill color="yellow" size={20} /> <p> <b>City </b>: Chennai, TN, India </p></Col> <br></br>
                </Row>
              </div>
            </Col>
          </Row>
          <Row>
            <Col>
              <div className="largetext" style={{ "textAlign": "left", textJustify: "center", "paddingTop": "50px" }}>
                <h5 className="resumeHeader" >Skills and Interest</h5>
                <div style={{ backgroundColor: "rgba(9, 30, 73,0.7)", paddingTop: "10px", borderRadius: "8px" }}>
                  <Row>
                    <Col lg={6}>
                      <Row>
                        <Col md={6}>
                          <div style={{ display: "flex", justifyContent: "space-around" }}>
                            <p className="html iconAlignCenter">
                              <img title="Html" alt="Html" style={{ width: "60px", height: "70px" }} src={html5}></img>
                              <span>HTML</span>
                            </p>
                            <p className="css iconAlignCenter">
                              <img title="CSS" alt="CSS" style={{ width: "60px", height: "70px" }} src={css3}></img>
                              <span>Css3</span>
                            </p>
                            <p className="Bootstrap iconAlignCenter">
                              <img title="Bootstrap" alt="Bootstrap" style={{ width: "60px", height: "70px" }} src={bootstrap}></img>
                              <span>Bootstrap</span>
                            </p>
                          </div>
                        </Col>
                        <Col md={6}>
                          <div style={{ display: "flex", justifyContent: "space-around" }}>
                            <p className="Angular iconAlignCenter">
                              <img title="Angular" alt="Angular" style={{ width: "60px", height: "70px" }} src={angular}></img>
                              <span>Angular</span>
                            </p>
                            <p className="React iconAlignCenter">
                              <img title="React" alt="React" style={{ width: "60px", height: "70px" }} src={react}></img>
                              <span>React</span>
                            </p>
                            <p className="Csharp iconAlignCenter">
                              <img title="csharp" alt="csharp" style={{ width: "60px", height: "70px" }} src={csharp}></img>
                              <span>C#</span>
                            </p>
                          </div>
                        </Col>
                      </Row>
                    </Col>
                    <Col lg={6}>
                      <Row>
                        <Col md={6}>
                          <div style={{ display: "flex", justifyContent: "space-around" }}>
                            <p className="Js iconAlignCenter">
                              <img title="javascript" alt="javascript" style={{ width: "60px", height: "70px" }} src={javascript}></img>
                              <span>Js</span>
                            </p>
                            <p className="Java iconAlignCenter">
                              <img title="Java" alt="Java" style={{ width: "60px", height: "70px" }} src={java}></img>
                              <span>Java</span>
                            </p>
                            <p className="NodeJs iconAlignCenter">
                              <img title="nodejs" alt="nodejs" style={{ width: "60px", height: "70px" }} src={nodejs}></img>
                              <span>NodeJs</span>
                            </p>
                          </div>
                        </Col>
                        <Col md={6}>
                          <div style={{ display: "flex", justifyContent: "space-around" }}>
                            <p className="Database iconAlignCenter">
                              <img title="database" alt="database" style={{ width: "60px", height: "70px" }} src={database}></img>
                              <span>SQL</span>
                            </p>
                            <p className="firebase iconAlignCenter">
                              <img title="firebase" alt="firebase" style={{ width: "60px", height: "70px" }} src={firebase}></img>
                              <span>Firebase</span>
                            </p>
                            <p className="Database iconAlignCenter">
                              <img title="photoshop" alt="photoshop" style={{ width: "60px", height: "70px" }} src={photoshop}></img>
                              <span>Photoshop</span>
                            </p>

                          </div>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </div>
              </div>
            </Col>
          </Row>
          <Row>
            <Col>
              <div className="largetext" style={{ "textAlign": "left", textJustify: "center", "paddingTop": "50px" }}>
                <h5 className="resumeHeader" >Social Media</h5>
                <div style={{ display: "flex" }}>
                  <a href="https://www.hackerrank.com/mdyasarbe" target="_blank" rel="noreferrer" 
                  className="social-buttons__button social-button social-button--hackerrank">
                    <FontAwesomeIcon title="HackerRank" icon={faHackerrank} size="4x"></FontAwesomeIcon>
                  </a>
                  <a href="https://github.com/mdyasarbe" target="_blank" rel="noreferrer"
                   className="social-buttons__button social-button social-button--github">
                    <FontAwesomeIcon title="Github" icon={faGithub} size="3x"></FontAwesomeIcon>
                  </a>
                  <a href="https://www.linkedin.com/in/mdyasarbe" target="_blank" rel="noreferrer"
                   className="social-buttons__button social-button social-button--linkedin">
                    <FontAwesomeIcon title="Linkedin" icon={faLinkedin} size="3x"></FontAwesomeIcon>
                  </a>
                </div>
              </div>

            </Col>
          </Row>
        </Container>


      </div>
    );
  }
}