import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';

import { FontAwesomeIcon, } from '@fortawesome/react-fontawesome';
import {  faInstagram, faLinkedin } from '@fortawesome/free-brands-svg-icons';
import { mail, social } from '../assets/index';


export class ContactComponent extends React.Component {

    render() {
        return (
            <div>
                <Container style={{ "paddingTop": "50px" }}>
                    <Row>
                        <Col md={12} style={{ backgroundColor: "rgba(255,255,255,0.1)", borderRadius: "8px" }}>
                            <div className="largetext" style={{ "textAlign": "left", textJustify: "center" }}>
                                <h4 className="resumeHeader" >Contact</h4>
                                <Row style={{ paddingBottom: "10px" }}>
                                    <Col md={6}>
                                        <Card>
                                            <Card.Header>
                                                <h5 >
                                                    <img src={mail} alt="mail" width="35px" />{"  "} <span style={{ color: "yellow" }}>Email</span>
                                                </h5>
                                            </Card.Header>
                                            <Card.Body style={{ height: "80px" }}>
                                                <Row>
                                                    <Col md="auto">
                                                        <a style={{ color: "white" }} href={`mailto:mdyasarbe@gmail.com`}><h5>mdyasarbe@gmail.com</h5></a>
                                                    </Col>
                                                </Row>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                    <Col md={6}>
                                        <Card>
                                            <Card.Header><h5><img src={social} alt="Social" width="35px" />{"  "}<span style={{ color: "yellow" }}>Social Media</span></h5></Card.Header>
                                            <Row>
                                                <Col md="auto" >
                                                    <Card.Body style={{ height: "80px" }}>
                                                        <div style={{ display: "flex" }}>
                                                            <a href="https://www.instagram.com/mdyasarbe" target="_blank" rel="noreferrer"
                                                                className="social-buttons__button social-button social-button--instagram">
                                                                <FontAwesomeIcon title="Instagram" icon={faInstagram} size="3x"></FontAwesomeIcon>
                                                            </a>
                                                            <a href="https://www.linkedin.com/in/mdyasarbe" target="_blank" rel="noreferrer"
                                                                className="social-buttons__button social-button social-button--linkedin">
                                                                <FontAwesomeIcon title="Linkedin" icon={faLinkedin} size="3x"></FontAwesomeIcon>
                                                            </a>
                                                        </div>
                                                    </Card.Body>
                                                </Col>
                                            </Row>
                                        </Card>
                                    </Col>
                                </Row>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div >
        );
    };

}