import React from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import {Container,Row, Col} from 'react-bootstrap';
import { Link } from "react-router-dom";
import { FontAwesomeIcon, } from '@fortawesome/react-fontawesome';
import { faHackerrank, faGithub, faLinkedin, faInstagram } from '@fortawesome/free-brands-svg-icons'

import { AutoTypingComponent } from './AutoTypingComponent';

export class NavigationBarComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentTab: "",
      location: ""
    };
  }
  componentDidMount() {
    var currtab = window.location.pathname;
    currtab = currtab.substring(1, currtab.length).toLocaleLowerCase();

    this.setState({
      currentTab: currtab
    });
    console.log(currtab);
  }
  onTabClick(tabName) {
    this.setState({
      currentTab: tabName
    });
  }

  render() {

    var isHome = window.location.pathname === "/" ? true : false;
    var home = {};
    var main = {};
    if (isHome) {
      home = {
        display: 'block',
      }
      main = {
        display: 'none',
       
      }

    } else {
      home = {
        display: 'none',
       
      }
      main = {
        display: 'block',
        
      }
    }

    return (
      <div>
        <div className='home' style={home}>
          <div className="NavbarforMobile">
            <Navbar collapseOnSelect={true} expand="sm" bg="dark" variant="dark">
              <Container >
                <div>
                  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                  <Navbar.Collapse id="responsive-navbar-nav">
                  <Nav className="me-auto">
                </Nav>
                    <Nav className="justify-content-end">
                    <Nav.Link onClick={() => this.onTabClick("")} eventKey="1"
                        active={this.state.currentTab === ""} as={Link} to="/">Home</Nav.Link>
                      <Nav.Link onClick={() => this.onTabClick("about")} eventKey="2"
                        active={this.state.currentTab === "about"} as={Link} to="/About">About</Nav.Link>
                      <Nav.Link onClick={() => this.onTabClick("education")} eventKey="3"
                        active={this.state.currentTab === "education"} as={Link} to="/Education">Education</Nav.Link>
                      <Nav.Link onClick={() => this.onTabClick("experience")} eventKey="4"
                        active={this.state.currentTab === "experience"} as={Link} to="/Experience">Experience</Nav.Link>
                  
                      <Nav.Link onClick={() => this.onTabClick("contact")} eventKey="5"
                        active={this.state.currentTab === "contact"} as={Link} to="/Contact">Contact</Nav.Link>
                    </Nav>
                  </Navbar.Collapse>
                </div>
              </Container>
            </Navbar>
          </div>
          <div className='HomeMain'>
            <div>
              <div className='heading'>
                <h1 style={{font:"sans-serif"}}>Mohamad Yasar J</h1>
                <AutoTypingComponent />
              </div>
              <div className='NavbarforLargeDiplay'>
                <Navbar collapseOnSelect expand="sm" style={{backgroundColor:"#212123"}} variant="dark">

                  <Navbar.Collapse style={{ paddingLeft: '5%' }} id="responsive-navbar-nav">
                    <Nav className="justify-content-end">
                    <Nav.Link onClick={() => this.onTabClick("")}
                        active={this.state.currentTab === ""} as={Link} to="/">Home</Nav.Link>
                      <Nav.Link onClick={() => this.onTabClick("about")}
                        active={this.state.currentTab === "about"} as={Link} to="/About">About</Nav.Link>
                      <Nav.Link onClick={() => this.onTabClick("education")}
                        active={this.state.currentTab === "education"} as={Link} to="/Education">Education</Nav.Link>
                      <Nav.Link onClick={() => this.onTabClick("experience")}
                        active={this.state.currentTab === "experience"} as={Link} to="/Experience">Experience</Nav.Link>
                      <Nav.Link onClick={() => this.onTabClick("contact")}
                        active={this.state.currentTab === "contact"} as={Link} to="/Contact">Contact</Nav.Link>
                    </Nav>
                  </Navbar.Collapse>
                </Navbar>
              </div> 
              <Row>
            <Col>
              <div className="largetext heading" style={{ "textAlign": "left", textJustify: "center", "paddingTop": "10px" }}>
                <div style={{ display: "flex" }}>
                  <a  href="https://www.hackerrank.com/mdyasarbe" target="_blank" rel="noreferrer" 
                   className="social-buttons__button social-button social-button--hackerrank">
                    <FontAwesomeIcon title="HackerRank" icon={faHackerrank} size="3x"></FontAwesomeIcon>
                  </a>
                  <a href="https://github.com/mdyasarbe" target="_blank" rel="noreferrer"
                    className="social-buttons__button social-button social-button--github">
                    <FontAwesomeIcon title="Github" icon={faGithub} size="3x"></FontAwesomeIcon>
                  </a>
                  <a href="https://www.linkedin.com/in/mdyasarbe" target="_blank" rel="noreferrer"
                    className="social-buttons__button social-button social-button--linkedin">
                    <FontAwesomeIcon title="Linkedin" icon={faLinkedin} size="3x"></FontAwesomeIcon>
                  </a>
                  <a href="https://www.instagram.com/mdyasarbe" target="_blank" rel="noreferrer"
                    className="social-buttons__button social-button social-button--instagram">
                    <FontAwesomeIcon title="Instagram" icon={faInstagram} size="3x"></FontAwesomeIcon>
                  </a>
                </div>
              </div>

            </Col>
          </Row>
            </div>
          </div>
        </div>
        <div style={main} className='main'>
          <Navbar collapseOnSelect expand="md" style={{backgroundColor:"#212123"}} variant="dark">
            <Container >
              <Navbar.Brand style={{color:"white"}} onClick={() => this.onTabClick("")}
                as={Link} to=""><h2 className="NavbarName" style={{font:"YesterYear"}}>Mohamad Yasar J</h2></Navbar.Brand>
              <Navbar.Toggle aria-controls="responsive-navbar-nav" />
              <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="me-auto">
                </Nav>
                <Nav >
                <Nav.Link onClick={() => this.onTabClick("")} eventKey="1"
                        active={this.state.currentTab === ""} as={Link} to="/">Home</Nav.Link>
                  <Nav.Link onClick={() => this.onTabClick("about")}  eventKey="2"
                    active={this.state.currentTab === "about"} as={Link} to="/About">About</Nav.Link>
                  <Nav.Link onClick={() => this.onTabClick("education")}  eventKey="3"
                    active={this.state.currentTab === "education"} as={Link} to="/Education">Education</Nav.Link>
                  <Nav.Link onClick={() => this.onTabClick("experience")} eventKey="4"
                    active={this.state.currentTab === "experience"} as={Link} to="/Experience">Experience</Nav.Link>
                  <Nav.Link onClick={() => this.onTabClick("contact")} eventKey="5"
                    active={this.state.currentTab === "contact"} as={Link} to="/Contact">Contact</Nav.Link>
                </Nav>
              </Navbar.Collapse>
            </Container>
          </Navbar>
        </div>
      </div>
    );
  }
}